import styles from './styles/sideMenu.module.css'

function SideMenu ({menuClick, changeMenuClicked}) {
    return (
        <nav className={`${menuClick ? "fixed transform translate-x-0 transition-all ease-out duration-500": "fixed transform -translate-x-11/12 transition-all ease-out duration-500 "} w-11/12 z-20 h-full bg-white`}
            onClick={changeMenuClicked}
        >
            {console.log("menu : ", menuClick ? "clicked ": "not clicked ")}
            <h1>Side Menu</h1>
        </nav>
    )
}

export default SideMenu